
.shopcart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 46px;
}
.shopcart .shopcart-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  background: #141d27;
}
.shopcart .shopcart-left .logo {
  display: inline-block;
  position: relative;
  top: -12px;
  left: 12px;
  padding: 6px;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  box-sizing: border-box;
  font-size: 24px;
  line-height: 24px;
  background: rgba(20,29,39,0.9);
}
.shopcart .shopcart-left .logo .icon-shopping_cart {
  display: inline-block;
  position: relative;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  text-align: center;
  line-height: 44px;
  color: rgba(255,255,255,0.4);
  background: rgba(0,0,0,0.9);
}
.shopcart .shopcart-left .logo .icon-shopping_cart.highlight {
  color: #fff;
  background: #00a0dc;
}
.shopcart .shopcart-left .price {
  display: inline-block;
  vertical-align: top;
  margin: 12px 12px 12px 18px;
  padding-right: 12px;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
  color: rgba(255,255,255,0.4);
}
.shopcart .shopcart-left .price.highlight {
  color: #fff;
}
.shopcart .shopcart-left .desc {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  font-size: 10px;
  line-height: 24px;
  color: rgba(255,255,255,0.4);
}
.shopcart .shopcart-left .count {
  position: absolute;
  left: 44px;
  top: -10px;
  padding: 0 6px;
  width: 24px;
  border-radius: 8px;
  box-sizing: border-box;
  font-size: 10px;
  font-weight: 700;
  line-height: 16px;
  text-align: center;
  color: #fff;
  background: #f01414;
}
.shopcart .shopcart-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 106px;
          flex: 0 0 106px;
  padding: 0 8px;
  background: #2b333b;
  text-align: center;
  box-sizing: border-box;
  color: rgba(255,255,255,0.4);
}
.shopcart .shopcart-right.highlight {
  background: #0d7aff;
  color: #fff;
}
.shopcart .shopcart-right .pay {
  font-size: 12px;
  font-weight: 700;
  line-height: 46px;
}


