
html,
body,
.detail-wrap {
  height: 100%;
}
.header {
  position: relative;
  overflow: hidden;
  color: #fff;
  background-color: rgba(7,17,27,0.5);
}
.header .content-wrap {
  position: relative;
  padding: 24px 12px 18px 24px;
  font-size: 0;
}
.header .content-wrap .avatar {
  display: inline-block;
}
.header .content-wrap .avatar>img {
  border-radius: 2px;
}
.header .content-wrap .content {
  display: inline-block;
  vertical-align: top;
  margin-left: 16px;
  font-size: 12px;
  font-weight: 200;
  line-height: 12px;
}
.header .content-wrap .content .title {
  margin: 2px 6px 8px 0;
}
.header .content-wrap .content .title .brand {
  background-image: url("./brand@2x.png");
  vertical-align: top;
  background-size: 30px 18px;
  background-repeat: no-repeat;
  display: inline-block;
  width: 30px;
  height: 18px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .title .brand {
    background-image: url("./brand@3x.png");
}
}
.header .content-wrap .content .title .name {
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}
.header .content-wrap .content .description {
  font-size: 10px;
}
.header .content-wrap .content .sell-supports {
  margin: 10px 0 2px 0;
}
.header .content-wrap .content .sell-supports .icon {
  background-size: 12px;
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: top;
  width: 12px;
  height: 12px;
}
.header .content-wrap .content .sell-supports .icon.decrease {
  background-image: url("./decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .sell-supports .icon.decrease {
    background-image: url("./decrease_1@3x.png");
}
}
.header .content-wrap .content .sell-supports .icon.discount {
  background-image: url("./discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .sell-supports .icon.discount {
    background-image: url("./discount_1@3x.png");
}
}
.header .content-wrap .content .sell-supports .icon.guarantee {
  background-image: url("./guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .sell-supports .icon.guarantee {
    background-image: url("./guarantee_1@3x.png");
}
}
.header .content-wrap .content .sell-supports .icon.invoice {
  background-image: url("./invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .sell-supports .icon.invoice {
    background-image: url("./invoice_1@3x.png");
}
}
.header .content-wrap .content .sell-supports .icon.special {
  background-image: url("./special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrap .content .sell-supports .icon.special {
    background-image: url("./special_1@3x.png");
}
}
.header .content-wrap .content .sell-supports .text {
  margin-left: 4px;
  font-size: 10px;
}
.header .content-wrap .supports-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  heigth: 24px;
  font-size: 0;
  background-color: rgba(0,0,0,0.2);
  border-radius: 16px;
  text-align: center;
}
.header .content-wrap .supports-count .count {
  margin-right: 2px;
  vertical-align: top;
  font-size: 10px;
  line-height: 24px;
}
.header .content-wrap .supports-count .icon-keyboard_arrow_right {
  line-height: 24px;
  font-size: 10px;
}
.header .bulletin-wrap {
  position: relative;
  padding: 0 22px 0 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  height: 28px;
  line-height: 28px;
  font-size: 0;
  background-color: rgba(7,17,27,0.3);
}
.header .bulletin-wrap .bulletin-icon {
  display: inline-block;
  vertical-align: top;
  margin-top: 8px;
  width: 22px;
  height: 12px;
  background-image: url("./bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrap .bulletin-icon {
    background-image: url("./bulletin@3x.png");
}
}
.header .bulletin-wrap .bulletin-text {
  display: inline-block;
  vertical-align: top;
  margin-left: 4px;
  font-size: 10px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
  overflow: auto;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
  transition: all 0.5s;
/*过度动画*/
/* stacike footers 布局 start */
}
.header .detail.fade-enter-active {
  -webkit-animation: fade-in 0.5s;
          animation: fade-in 0.5s;
}
.header .detail.fade-leave-active {
  -webkit-animation: fade-out 0.5s;
          animation: fade-out 0.5s;
}
.header .detail .detail-wrap {
  width: 100%;
  height: auto;
  min-height: 100%;
}
.header .detail .detail-wrap .detail-main {
  margin-top: 64px;
  padding: 0 36px;
  padding-bottom: 64px;
}
.header .detail .detail-wrap .detail-main .name {
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
  color: #fff;
}
.header .detail .detail-wrap .detail-main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 28px 0 24px;
  width: 100%;
}
.header .detail .detail-wrap .detail-main .title .text {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 0 12px;
  text-align: center;
  font-size: 14px;
  line-height: 14px;
  font-weight: 700;
  color: #fff;
}
.header .detail .detail-wrap .detail-main .title .line {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-top: 7px;
  border-top: 1px solid rgba(255,255,255,0.2);
}
.header .detail .detail-wrap .detail-main .item-support .text {
  vertical-align: top;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  color: #fff;
}
.header .detail .detail-wrap .detail-main .item-support .icon {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 12px;
  margin-left: 12px;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
}
.header .detail .detail-wrap .detail-main .item-support .icon.decrease {
  background-image: url("./decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrap .detail-main .item-support .icon.decrease {
    background-image: url("./decrease_2@3x.png");
}
}
.header .detail .detail-wrap .detail-main .item-support .icon.discount {
  background-image: url("./discount_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrap .detail-main .item-support .icon.discount {
    background-image: url("./discount_2@3x.png");
}
}
.header .detail .detail-wrap .detail-main .item-support .icon.special {
  background-image: url("./special_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrap .detail-main .item-support .icon.special {
    background-image: url("./special_2@3x.png");
}
}
.header .detail .detail-wrap .detail-main .item-support .icon.invoice {
  background-image: url("./invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrap .detail-main .item-support .icon.invoice {
    background-image: url("./invoice_2@3x.png");
}
}
.header .detail .detail-wrap .detail-main .item-support .icon.guarantee {
  background-image: url("./guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-wrap .detail-main .item-support .icon.guarantee {
    background-image: url("./guarantee_2@3x.png");
}
}
.header .detail .detail-wrap .detail-main .bulletin {
  margin: 24px 12px 0;
  font-size: 12px;
  font-weight: 200;
  line-height: 24px;
  color: #fff;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
  color: rgba(255,255,255,0.5);
}
@-webkit-keyframes fade-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes fade-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes fade-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes fade-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}


