
.controlbtn {
  font-size: 0;
}
.controlbtn .add {
  vertical-align: top;
  display: inline-block;
  padding: 6px;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.controlbtn .sub {
  display: inline-block;
  padding: 6px 6px 4px 6px;
}
.controlbtn .sub.move-leave-active,
.controlbtn .sub.move-enter-active {
  transition: all 0.4s linear;
}
.controlbtn .sub.move-enter,
.controlbtn .sub.move-leave-active {
  opacity: 0;
  -webkit-transform: transilate3D(24px, 0, 0);
          transform: transilate3D(24px, 0, 0);
}
.controlbtn .sub .inner {
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
  line-height: 24px;
  color: #00a0dc;
}
.controlbtn .sub .inner.inner-leave-active,
.controlbtn .sub .inner.inner-enter-active {
  transition: all 0.4s linear;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
}
.controlbtn .sub .inner.inner-enter,
.controlbtn .sub .inner.inner-leave-active {
  opacity: 0;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.controlbtn .count {
  display: inline-block;
  vertical-align: top;
  padding-top: 6px;
  width: 12px;
  font-size: 10px;
  line-height: 24px;
  text-align: center;
  color: #93999f;
}


